import { Component, OnInit, ViewChild } from '@angular/core';
import { emailSentBarChart, monthlyEarningChart } from './data';
import { ChartType } from './dashboard.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { EventService } from '../../../core/services/event.service';

import { ConfigService } from '../../../core/services/config.service';
import { ActividadService } from '../../actividad/service/actividad.service';
import { Actividad } from '../../actividad/models/actividad';
import { Subject } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { DetalleActividadService } from '../../actividad/service/detalle-actividad.service';
import { HorasActividad } from '../../actividad/models/detalleactividad';
import { AutenticacionService } from 'src/app/account/auth/login/service/autenticacion.service';
import { UsuarioA } from 'src/app/account/auth/login/models/usuario';
import Swal from 'sweetalert2';
import { Role } from 'src/app/enum/role.enum';

@Component({
  selector: 'app-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent implements OnInit {

  isVisible: string;

  emailSentBarChart: ChartType;
  monthlyEarningChart: ChartType;
  transactions: Array<[]>;
  statData: Array<[]>;
  fe
  isActive: string;

  userauth!: UsuarioA
  idEstudiantePersona: number;
  actividades: Actividad[] = [];
  actividadesCont: Actividad[] = [];
  horasAcumuladas: HorasActividad[] = [];
  horas: string;
  activosCount = 0;

  //------------------
  /**DataTables */
  @ViewChild(DataTableDirective, { static: false }) dtElement: DataTableDirective
  dtOptions: any = {};
  dtTrigger: Subject<Actividad> = new Subject<Actividad>();


  @ViewChild('content') content;
  constructor(private modalService: NgbModal, private configService: ConfigService,
    private eventService: EventService,
    private actividadServicioDash: ActividadService,
    private detalleServiceDash: DetalleActividadService,
    private authService: AutenticacionService,) {
  }

  ngOnInit() {

    /**
     * horizontal-vertical layput set
     */
    const attribute = document.body.getAttribute('data-layout');

    this.isVisible = attribute;
    const vertical = document.getElementById('layout-vertical');
    if (vertical != null) {
      vertical.setAttribute('checked', 'true');
    }
    if (attribute == 'horizontal') {
      const horizontal = document.getElementById('layout-horizontal');
      if (horizontal != null) {
        horizontal.setAttribute('checked', 'true');
        console.log(horizontal);
      }
    }


    /**
     * Fetches the data
     */
    // this.fetchData();
    this.userauth = this.authService.getUserFromLocalCache();
    this.idEstudiantePersona = this.userauth.estudiante.id;
    this.dtOptions = {
      columnDefs: [
        { className: "", targets: [0, 1, 2] },
        { orderable: false, targets: [3] },
        { searchable: false, targets: [0, 3] },

        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: '//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json',
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningn dato encontrado",
      },
      pagingType: 'full_numbers',
      responsive: true,
    };
    this.cargarDatosActividadesDash();
    this.fecha()
    this.cargarHorasTotales();
    this.getActividadesRegistradas();
  }

  ngOnDestroy() { //para destruir la peticion
    this.dtTrigger.unsubscribe();
    // this.dtTrigger.complete()
  }



  cargarDatosActividadesDash() {
    this.actividadServicioDash.mostrarActividadesListEstudiante(this.idEstudiantePersona).subscribe((resp: any) => {
      this.actividades = resp;
      this.dtTrigger.next(null);
    });

  }

  cargarHorasTotales() {
    this.detalleServiceDash.mostrarHorasActividadesAcumuladas(this.idEstudiantePersona).subscribe((resp: HorasActividad[]) => {
      if (resp && resp.length > 0) {
        this.horasAcumuladas = resp;
        this.horas = this.horasAcumuladas[0].horas;

        const partes = this.horas.split(':');
        // Convierte las partes a nmeros enteros
        const horas = parseInt(partes[0]);
        const minutos = parseInt(partes[1]);
        // Convierte en horas y minutos
        const horasLimite = 500;
        const minutosLimite = 0;

        if (horas > horasLimite || (horas === horasLimite && minutos >= minutosLimite)) {
              this.Toast.fire({
              icon: 'info',
              title: 'xito completo el total de horas requeridas!'
            });
        } else {
          // console.log("La variable this.horas es menor a 500:00");
        }

      } else {
        // Error en el caso que no se encuentra ningn elemento en resp
        //console.error("No se encontraron horasAcumuladas en la respuesta");
        this.horas = "0";
      }
    });
  }


  getActividadesRegistradas() {
    this.actividadServicioDash
      .actividadesReg(this.userauth.id)
      .subscribe((resp: Actividad[]) => {
        this.actividadesCont = resp;
        this.actividadesCont.forEach((actividad) => {
          if (actividad.estado === 'Activo') {
            this.activosCount++;
          }
        });
        // this.dtTrigger.next(null);
      });


  }

  // ngAfterViewInit() {
  //   setTimeout(() => {
  //     this.openModal();
  //   }, 2000);
  // }

  /**
   * Fetches the data
   */
  private fetchData() {
    this.emailSentBarChart = emailSentBarChart;
    this.monthlyEarningChart = monthlyEarningChart;

    this.isActive = 'year';
    this.configService.getConfig().subscribe(data => {
      this.transactions = data.transactions;
      this.statData = data.statData;
    });
  }

  fecha() {
    const f = new Date();
    this.fe = f.getDate() + "/" + (f.getMonth() + 1) + "/" + f.getFullYear();


  }

  openModal() {
    this.modalService.open(this.content, { centered: true });
  }

  public Toast = Swal.mixin({
    toast: true,
    position: 'top-end',
    showConfirmButton: false,
    showCloseButton: true,
    timer: 7000,
    timerProgressBar: true,
    didOpen: (toast) => {
      toast.addEventListener('mouseenter', Swal.stopTimer)
      toast.addEventListener('mouseleave', Swal.resumeTimer)
    }
  })


  getPermisos() {
    return this.authService.hasRole(Role.ROLE_ESTUDIANTE);
  }
}
