import { Component, OnInit, ViewChild } from "@angular/core";
import { EstudianteService } from "../../estudiante/service/estudiante.service";
import { DataTableDirective } from "angular-datatables";
import { EstudianteActividad } from "../../estudiante/models/estudiante";
import { Subject } from "rxjs";
import { Actividad } from "../../actividad/models/actividad";
import { AutenticacionService } from "src/app/account/auth/login/service/autenticacion.service";
import { UsuarioA } from "src/app/account/auth/login/models/usuario";
import { NgbModal } from "@ng-bootstrap/ng-bootstrap";
import { EquipoService } from "../../equipo/service/equipo.service";
import { Equipo } from "../../equipo/models/equipo";
import { Role } from "src/app/enum/role.enum";

@Component({
  selector: "app-dashadmin",
  templateUrl: "./dashadmin.component.html",
  styleUrls: ["./dashadmin.component.scss"],
})
export class DashadminComponent implements OnInit {
  estudiante!: EstudianteActividad;
  actividades: EstudianteActividad[] = [];
  actividad: EstudianteActividad;
  user!: UsuarioA;
  equipos: Equipo[] = [];
  fe: string;
  activosCount = 0;
  buenosCount = 0;
  danadosCount = 0;
  obsoletosCount = 0;
  /**DataTables */
  @ViewChild(DataTableDirective, { static: false })
  dtElement: DataTableDirective;
  dtOptions: any = {};
  dtTrigger: Subject<Actividad> = new Subject<Actividad>();
  constructor(
    private estudianteService: EstudianteService,
    private authService: AutenticacionService,
    private modalService: NgbModal,
    private esquipoService: EquipoService
  ) { }

  ngOnInit(): void {
    this.user = this.authService.getUserFromLocalCache();
    this.loadable();
    this.getActividadesRegistradas();
    this.cargarDatos()
  }
  loadable() {
    this.dtOptions = {
      columnDefs: [
        { className: "", targets: [0, 1, 2] },
        { orderable: false, targets: [2] },
        { searchable: false, targets: [0, 2] },

        { responsivePriority: 1, targets: -1 },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: "//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json",
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningn dato encontrado",
      },
      pagingType: "full_numbers",
      responsive: true,
    };
  }
  getActividadesRegistradas() {
    this.estudianteService
      .actividadesReg(this.user.id)
      .subscribe((resp: EstudianteActividad[]) => {
        this.actividades = resp;
        this.actividades.forEach((actividad) => {
          if (actividad.estado === 'Activo') {
            this.activosCount++;
          }
        });
        this.dtTrigger.next(null);
      });


  }
  openModal(content: any, e: EstudianteActividad) {
    this.actividad = e;
    this.modalService.open(content, {
      backdrop: "static",
      keyboard: false,
      size: "lg",
    });
  }
  fecha() {
    const f = new Date();
    this.fe = f.getDate() + "/" + (f.getMonth() + 1) + "/" + f.getFullYear();
  }
  cargarDatos() {
    this.esquipoService.equipos().subscribe((resp: any) => {
      this.equipos = resp;
      // Itera sobre los equipos y cuenta en qu categora se encuentran
      this.equipos.forEach((equipo) => {
        if (equipo.estado === 'Bueno') {
          this.buenosCount++;
        } else if (equipo.estado === 'Daado') {
          this.danadosCount++;
        } else if (equipo.estado === 'Obsoleto') {
          this.obsoletosCount++;
        }
      });

    });
  }
}
