import { Component, Input, OnInit } from "@angular/core";
import { EstudianteActividad } from "../../estudiante/models/estudiante";
import { EstudianteService } from "../../estudiante/service/estudiante.service";
import { Subject } from "rxjs";

@Component({
  selector: "app-estudiantes",
  templateUrl: "./estudiantes.component.html",
  styleUrls: ["./estudiantes.component.scss"],
})
export class EstudiantesComponent implements OnInit {
  listaEstudiantes: EstudianteActividad[] = [];
  @Input() actividad: EstudianteActividad;
  dtOptions: any = {};
  dtTriggerr: Subject<EstudianteActividad> = new Subject<EstudianteActividad>();
  constructor(
    private estudianteService: EstudianteService,
  ) {}

  ngOnInit(): void {
    this.loadTable()
    this.cargarDatos()
  }
  loadTable(){
    this.dtOptions = {
      searching: false,
      columnDefs: [
        { orderable: false, targets: [0] },
        { searchable: false, targets: [0] },
      ],
      lengthMenu: [5, 10, 20, 50],
      destroy: true,
      language: {
        url: "//cdn.datatables.net/plug-ins/1.13.5/i18n/es-ES.json",
        lengthMenu: "Mostrar _MENU_ registros",
        zeroRecords: "Ningn dato encontrado",
      },
      pagingType: "full_numbers",
      responsive: true,
    };
  }
  cargarDatos() {
    this.estudianteService
      .actividadxEstudiante(this.actividad.id)
      .subscribe((resp: EstudianteActividad[]) => {
        this.listaEstudiantes = resp;
        this.dtTriggerr.next()
      });
  }
}
