import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { Actividad, ActividadEstado, ActividadLista, ActividadMostrar } from '../models/actividad';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class ActividadService {
  url = `${environment.urlG}`
  constructor(private http: HttpClient) { }

  public mostrarActividadesListEstudiante(idEstudianteParam: number): Observable<any> {
    return this.http.get<ActividadMostrar[]>(`${this.url}/actividad/listAllActividadHoras?idEstudianteParam=${idEstudianteParam}`);

  }

 public nuevaActividad(actividad: Actividad) {
    return this.http.post(`${this.url}/actividad/guardarActividad`, actividad);
  }

  public editarActividad(actividad: Actividad) {
    return this.http.put(`${this.url}/actividad/editarActividad`, actividad);
  }

 public actividadById(id: string): Observable<Actividad> {
    return this.http.get<Actividad>(`${this.url}/actividad/findBy/${id}`);
  }

  public getActividadByIdClase(id: string): Observable<Actividad> {
    return this.http.get<Actividad>(`${this.url}/actividad/findByIdActividad/${id}`);
  }

  public actividadesRegistradas(id: number) {
    return this.http.get<ActividadLista[]>(`${this.url}/actividad/listAllActividad?id=${id}`);
  }

  public actividadesRegistradasConEstado(estado:string,idUsuario: number) {
    return this.http.get<ActividadLista[]>(`${this.url}/actividad/estado/${estado}?idUsuario=${idUsuario}`);
  }

  public editarEstadoAct(id: number,actividad: ActividadEstado) {
    return this.http.put(`${this.url}/actividad/editarEstadoAct/${id}`, actividad);
  }

  public generarPdfActividadesRealizadas(idEstudiante: number) {
    return this.http.get<Blob>(`${this.url}/actividad/pdf/actividadesrealizadas?idEstudiante=${idEstudiante}`, { responseType: 'arraybuffer' as 'json' });
  }

  public actividadesReg(x: number) {
    return this.http.get<Actividad[]>(`${this.url}/actividad/actividad/${x}`);
  }
}
