import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DefaultComponent } from './dashboards/default/default.component';
import { Role } from '../enum/role.enum';
import { RoleGuard } from '../core/guards/role.guard';
import { DashadminComponent } from './dashboards/dashadmin/dashadmin.component';
import { DredictComponent } from './dashboards/dredict/dredict.component';
import { AutenticacionGuard } from '../core/guards/autenticacion.guard';
import { AcercadeComponent } from '../acercade/acercade.component';

const routes: Routes = [
  { path: '', redirectTo: 'dredict' },
  { path: 'dredict', component: DredictComponent },
  { path: 'default', component: DefaultComponent, canActivate: [RoleGuard], data: { roles: [Role.ROLE_ESTUDIANTE] }, },

  { path: 'dash', component: DashadminComponent, canActivate: [RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES, Role.ROLE_PASANTE] }, },

  { path: 'dashboards', loadChildren: () => import('./dashboards/dashboards.module').then(m => m.DashboardsModule), canActivate: [AutenticacionGuard, RoleGuard] },
  {
    path: 'estudiante', loadChildren: () => import('./estudiante/estudiante.module').then(m => m.EstudianteModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES] },
  },
  {
    path: 'carrera', loadChildren: () => import('./carrera/carrera.module').then(m => m.CarreraModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES] },
  },
  {
    path: 'administrador', loadChildren: () => import('./administrador/administrador.module').then(m => m.AdministradorModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES] },
  },
  {
    path: 'cargo', loadChildren: () => import('./cargo/cargo.module').then(m => m.CargoModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES] },
  },
  {
    path: 'presupuesto', loadChildren: () => import('./presupuesto/presupuesto.module').then(m => m.PresupuestoModule),
    canActivate: [RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES, Role.ROLE_PASANTE] },
  },
  {
    path: 'actividad', loadChildren: () => import('./actividad/actividad.module').then(m => m.ActividadModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES, Role.ROLE_ESTUDIANTE] },
  },
  {
    path: 'categoria', loadChildren: () => import('./categoria/categoria.module').then(m => m.CategoriaModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES, Role.ROLE_PASANTE, Role.ROLE_ESTUDIANTE] },
  },
  {
    path: 'ubicacion', loadChildren: () => import('./ubicacion/ubicacion.module').then(m => m.UbicacionModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES, Role.ROLE_ESTUDIANTE, Role.ROLE_PASANTE] },
  },
  {
    path: 'mantenimiento', loadChildren: () => import('./mantenimiento/mantenimiento.module').then(m => m.MantenimientoModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES, Role.ROLE_PASANTE, Role.ROLE_ESTUDIANTE] },
  },
  {
    path: 'equipo', loadChildren: () => import('./equipo/equipo.module').then(m => m.EquipoModule),
    canActivate: [AutenticacionGuard, RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES, Role.ROLE_ESTUDIANTE, Role.ROLE_PASANTE] },
  },
  {
    path: 'edificio', loadChildren: () => import('./edificio/edificio.module').then(m => m.EdificioModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES, Role.ROLE_ESTUDIANTE, Role.ROLE_PASANTE] },
  },
  {
    path: 'usuario', loadChildren: () => import('./usuario/usuario.module').then(m => m.UsuarioModule),
    canActivate: [AutenticacionGuard,RoleGuard], data: { roles: [Role.ROLE_SUPER_ADMIN, Role.ROLE_ADMIN_REDES] },
  },
  { path: 'perfil', loadChildren: () => import('./perfil/perfil.module').then(m => m.PerfilModule), canActivate: [AutenticacionGuard]},
  { path: 'acerca', component:AcercadeComponent, canActivate: [AutenticacionGuard]},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PagesRoutingModule { }
