import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import Swal from 'sweetalert2';
import { catchError, map } from 'rxjs/operators';
import { Equipo } from '../models/equipo';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})
export class EquipoService {
  url = `${environment.urlG}/equipo`
  constructor(private http: HttpClient) { }

  public equipos(): Observable<any> {
    return this.http.get<any>(`${this.url}`)
  }

  public categorias(): Observable<any> {
    return this.http.get<any>(`${this.url}/categorias`)
  }

  public ubicaciones(): Observable<any> {
    return this.http.get<any>(`${this.url}/ubicaciones`)
  }

  guardar(equipo: Equipo) {
    const url = `${this.url}/guardar`;
    return this.http.post(url, equipo);
  }

  public editarEquipo(equipo: Equipo): any {
    return this.http.put(`${this.url}/modificar`, equipo);
  }



  eliminar(id: number): Observable<Equipo> {
    return this.http.delete<Equipo>(`${this.url}/${id}`).pipe(
      catchError(e => {
        Swal.fire('error', e.error.error)
        return throwError(e);
      })
    )
  }

  generarPdfEquipoxEstado(estado: string) {
    // const httpOptions={responseType:'arraybuffer' as 'json'};
    return this.http.get<Blob>(`${this.url}/pdf/estado` + '?' + `estado=${estado}`,  {responseType:'arraybuffer' as 'json'});
  }
  generarPdfFullEquipo() {
    return this.http.get<Blob>(`${this.url}/pdf/full`, {responseType:'arraybuffer' as 'json'});
  }
}
