import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { AutenticacionService } from 'src/app/account/auth/login/service/autenticacion.service';
import Swal from 'sweetalert2';

@Injectable({
  providedIn: 'root'
})
export class RoleGuard implements CanActivate {
  constructor(
    private authService: AutenticacionService,
    private router: Router

  ) { }
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    //   if(!this.authService.isUserLoggedIn){
    //     this.router.navigate(['account/login']);
    //     return false;
    //   }
    // let role = route.data['role'] as string;
    // if (this.authService.hasRole(role)) {
    //   return true;
    // }
    // Swal.fire('Error', 'No tiene permisos para acceder a este recurso', 'warning')
    // return false;
    if (!this.authService.isUserLoggedIn) {
      this.router.navigate(['account/login']);
      return false;
    }
    const roles = route.data['roles'] as string[];
    if (this.authService.hasRoles(roles)) {
      return true;
    } else {
      Swal.fire({
        confirmButtonColor: "#a90000",
        title: 'Advertencia',
        text:  'No tiene permisos para acceder a este recurso',
        icon: 'warning'
      });
        this.router.navigate(['dredict']);
        return false;
    }
  }

}
