import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { C, Carrera, Estudiante, EstudianteActividad, EstudianteList, Users } from '../models/estudiante';
import { DetalleActividad, DetalleActividadEstado, DetalleActividadMostrar } from '../../actividad/models/detalleactividad';
import { map } from 'jquery';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EstudianteService {
  url = `${environment.urlG}/estudiante`
  urlC = `${environment.urlG}/carrera`
  urlDt= `${environment.urlG}/detalleactividad`

  constructor(
    private http: HttpClient
  ) { }
  public estudiantes(): Observable<EstudianteList[]> {
    return this.http.get<EstudianteList[]>(`${this.url}`)
  }
  public estudiantesFull(): Observable<EstudianteList[]> {
    return this.http.get<EstudianteList[]>(`${this.url}/full`)
  }
  public estudiantesEstado(x: String): Observable<EstudianteList[]> {
    return this.http.get<EstudianteList[]>(`${this.url}/estado/${x}`)
  }
  public carrera(): Observable<Carrera[]> {
    return this.http.get<Carrera[]>(`${this.url}/carrera`)
  }
  public guardar(x: Estudiante) {
    return this.http.post(`${this.url}`, x)
  }
  public editar(x: Estudiante) {
    return this.http.put(`${this.url}/${x.id}`, x)
  }
  public eliminar(x: EstudianteList) {
    return this.http.delete(`${this.url}/${x.id}`)
  }
  public guardarCarrera(x: C) {
    return this.http.post(`${this.urlC}`, x)

  }
  // public users(): Observable<Users[]> {
  //   return this.http.get<Users[]>(`${this.url}/users`)
  // }
  public detallesEstudiante(x: number): Observable<EstudianteList[]> {
    return this.http.get<EstudianteList[]>(`${this.url}/detallesAct/${x}`)
  }
  generarPdfEstudiantexEstado(estado: string) {
    return this.http.get<Blob>(`${this.url}/pdf/estado` + '?' + `estado=${estado}`, { responseType: 'arraybuffer' as 'json' });
  }
  generarPdfFullEstudiante() {
    return this.http.get<Blob>(`${this.url}/pdf/full`, { responseType: 'arraybuffer' as 'json' });
  }
  public actividadxEstudiante(x: number) {
    return this.http.get<EstudianteActividad[]>(`${this.url}/xactividad/${x}`)
  }
  public actividadesReg(x: number) {
    return this.http.get<EstudianteActividad[]>(`${this.url}/actividad/${x}`);
  }
  public mostrarActividadesDetalleList(idEstuParam: number,idActividad: number): Observable<any> {
    return this.http.get<DetalleActividadMostrar[]>(`${this.url}/listAllDetalleActividades?idEstuParam=${idEstuParam}&idActividad=${idActividad}`);

  }
  public editarEstadoDt(id:number,x:DetalleActividadEstado) {
    return this.http.put(`${this.urlDt}/editarEstadoDetalleAct/${id}`, x)
  }


}
