import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { ConfirmarEstado, DetalleActividad, DetalleActividadMostrar, Ejecucion, HorasActividad, Ubicacion } from '../models/detalleactividad';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class DetalleActividadService {

  url = `${environment.urlG}`
  constructor(private http: HttpClient) { }


  public mostrarActividadesDetalleList(idEstuParam: number,idActividad: number): Observable<any> {
    return this.http.get<DetalleActividadMostrar[]>(`${this.url}/detalleactividad/listAllDetalleActividades?idEstuParam=${idEstuParam}&idActividad=${idActividad}`);

  }

  public confirmarEstadoActividad(idActividad: number): Observable<any> {
    return this.http.get<ConfirmarEstado[]>(`${this.url}/detalleactividad/confimacionEstado?idActividadParam=${idActividad}`);

  }


  public confirEstadoEjecucion(idActividad: number,idEstudiante:number): Observable<any> {
    return this.http.get<ConfirmarEstado[]>(`${this.url}/detalleactividad/confirEstadoEje?idActividadParam=${idActividad}&idEstu=${idEstudiante}`);

  }

  public confirEstadoFinalizadoValid(idActividad: number,idEstudiante:number): Observable<any> {
    return this.http.get<ConfirmarEstado[]>(`${this.url}/detalleactividad/confirEstadoFin?idActividadParam=${idActividad}&idEstu=${idEstudiante}`);

  }

  public nuevoDetalleActividadEstu(detalleactividad: DetalleActividad) {
    return this.http.post(`${this.url}/detalleactividad/guardarDetalleAct`, detalleactividad);
  }

  public editarDetalleActividadEstu(detalleactividad:DetalleActividad):any{
    return this.http.put(`${this.url}/detalleactividad/editarDetalleActividad`,detalleactividad);
  }

  public deleteDetalleActividadEstu(id:DetalleActividad){
    return this.http.delete(`${this.url}/detalleactividad/${id}`);
  }

  public nuevoEjecucion(ejecucion: Ejecucion) {
    return this.http.post(`${this.url}/detalleactividad/guardarEjecucion`, ejecucion);
  }


  public mostrarUbicaciones(): Observable<any> {
    return this.http.get<Ubicacion[]>(`${this.url}/ubicacion`);

  }
  public mostrarHorasActividadesAcumuladas(idEstudiante:number): Observable<HorasActividad[]> {
    return this.http.get<HorasActividad[]>(`${this.url}/detalleactividad/horasTotales?idEstudiante=${idEstudiante}`);

  }


}
