import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AutenticacionService } from 'src/app/account/auth/login/service/autenticacion.service';
import { Role } from 'src/app/enum/role.enum';

@Component({
  selector: 'app-dredict',
  templateUrl: './dredict.component.html',
  styleUrls: ['./dredict.component.scss']
})
export class DredictComponent implements OnInit {

  constructor(
    private authService: AutenticacionService,
    private router: Router,
  ) { }

  ngOnInit(): void {
    if (this.authService.isUserLoggedIn()) {

      if (this.authService.hasRole(Role.ROLE_ESTUDIANTE)){
        this.router.navigateByUrl('/default');
      }else{
        this.router.navigateByUrl('/dash');
      }

    }
  }

}
